"use client";

import { useMemo, useState } from "react";
import { steps, STYLE_EXAMPLES, type StyleId, type TemplateId, type Question } from "./steps";

type Answers = Record<string, any>;

const THEMES: Record<TemplateId, { name: string; bg: string; card: string; text: string; muted: string; accent: string; border: string }> = {
  "bleu-or": {
    name: "Bleu nuit / Or",
    bg: "bg-[#0B1B33]",
    card: "bg-[#0A0F1A]/60",
    text: "text-[#F7F4EE]",
    muted: "text-[#F7F4EE]/70",
    accent: "text-[#D6B46A]",
    border: "border-[#D6B46A]/30",
  },
  papier: {
    name: "Papier / Encre",
    bg: "bg-[#F7F4EE]",
    card: "bg-white",
    text: "text-[#0A0F1A]",
    muted: "text-[#0A0F1A]/60",
    accent: "text-[#0B1B33]",
    border: "border-black/10",
  },
  minimal: {
    name: "Minimal",
    bg: "bg-neutral-50",
    card: "bg-white",
    text: "text-neutral-900",
    muted: "text-neutral-600",
    accent: "text-neutral-900",
    border: "border-neutral-200",
  },
};

function cx(...c: Array<string | false | undefined>) {
  return c.filter(Boolean).join(" ");
}

export default function QuestionnairePage() {
  const [template, setTemplate] = useState<TemplateId>("bleu-or");
  const [styleId, setStyleId] = useState<StyleId>("narratif");
  const [stepIndex, setStepIndex] = useState(0);
  const [answers, setAnswers] = useState<Answers>({});

  const theme = THEMES[template];
  const step = steps[stepIndex];

  const progress = useMemo(() => {
    const pct = Math.round(((stepIndex + 1) / steps.length) * 100);
    return Math.min(100, Math.max(0, pct));
  }, [stepIndex]);

  function setAnswer(id: string, value: any) {
    setAnswers((a) => ({ ...a, [id]: value }));
  }

  function next() {
    setStepIndex((i) => Math.min(steps.length - 1, i + 1));
  }

  function prev() {
    setStepIndex((i) => Math.max(0, i - 1));
  }

  return (
    <main className={cx("min-h-screen", theme.bg, "p-6 flex items-center justify-center")}>
      <div className="w-full max-w-2xl">
        {/* header */}
        <div className={cx("mb-5", theme.text)}>
          <div className="flex items-start justify-between gap-4">
            <div>
              <p className={cx("text-sm", theme.muted)}>Et j’ai crié — Mémoire</p>
              <h1 className="text-2xl font-semibold mt-1">{step.title}</h1>
              <p className={cx("mt-2 text-sm", theme.muted)}>
                Rien n’est publié sans validation. Tout est ajustable.
              </p>
            </div>

            {/* template switch */}
            <div className={cx("rounded-2xl border px-3 py-2", theme.border, theme.card, theme.text)}>
              <p className={cx("text-xs mb-2", theme.muted)}>Template</p>
              <select
                value={template}
                onChange={(e) => setTemplate(e.target.value as TemplateId)}
                className={cx("text-sm rounded-xl px-2 py-1 bg-transparent border", theme.border)}
              >
                <option value="bleu-or">Bleu nuit / Or</option>
                <option value="papier">Papier / Encre</option>
                <option value="minimal">Minimal</option>
              </select>
            </div>
          </div>

          {/* progress */}
          <div className="mt-4">
            <div className={cx("h-2 rounded-full overflow-hidden border", theme.border)}>
              <div className="h-full bg-[#D6B46A]" style={{ width: `${progress}%` }} />
            </div>
            <p className={cx("mt-2 text-xs", theme.muted)}>
              Étape {stepIndex + 1} / {steps.length}
            </p>
          </div>
        </div>

        {/* card */}
        <div className={cx("rounded-3xl border p-6 shadow-sm", theme.card, theme.border, theme.text)}>
          {step.kind === "style-picker" ? (
            <StylePicker theme={theme} value={styleId} onChange={setStyleId} />
          ) : (
            <div className="space-y-5">
              {step.questions.map((q) => (
                <QuestionField
                  key={q.id}
                  q={q}
                  theme={theme}
                  value={answers[q.id]}
                  onChange={(v) => setAnswer(q.id, v)}
                />
              ))}
            </div>
          )}

          {/* footer */}
          <div className="mt-8 flex items-center justify-between gap-3">
            <button
              type="button"
              onClick={prev}
              disabled={stepIndex === 0}
              className={cx(
                "rounded-xl px-4 py-2 text-sm border transition",
                theme.border,
                stepIndex === 0 ? "opacity-40 cursor-not-allowed" : "hover:opacity-90"
              )}
            >
              Retour
            </button>

            <button
              type="button"
              onClick={next}
              className={cx(
                "rounded-xl px-5 py-2 text-sm font-medium transition",
                "bg-[#D6B46A] text-[#0A0F1A] hover:brightness-95 active:brightness-90"
              )}
            >
              Continuer
            </button>
          </div>

          <p className={cx("mt-4 text-xs", theme.muted)}>
            (Bientôt) Sauvegarde auto, accès famille, et génération du texte selon le style choisi.
          </p>
        </div>

        {/* debug mini (optionnel) */}
        <p className={cx("mt-4 text-xs", theme.muted)}>
          Style choisi : <span className={cx("font-medium", theme.accent)}>{STYLE_EXAMPLES[styleId].title}</span>
        </p>
      </div>
    </main>
  );
}

function StylePicker({
  value,
  onChange,
  theme,
}: {
  value: StyleId;
  onChange: (v: StyleId) => void;
  theme: any;
}) {
  return (
    <div>
      <p className={cx("text-sm mb-4", theme.muted)}>
        Choisis le ton qui te semble le plus juste. L’IA s’alignera dessus.
      </p>

      <div className="grid gap-4">
        {(Object.keys(STYLE_EXAMPLES) as StyleId[]).map((id) => {
          const ex = STYLE_EXAMPLES[id];
          const active = id === value;
          return (
            <button
              key={id}
              type="button"
              onClick={() => onChange(id)}
              className={cx(
                "text-left rounded-2xl border p-4 transition",
                theme.border,
                active ? "ring-2 ring-[#D6B46A]/50" : "hover:opacity-95"
              )}
            >
              <div className="flex items-center justify-between">
                <p className={cx("font-semibold", theme.text)}>{ex.title}</p>
                <span className={cx("text-xs", theme.accent)}>{active ? "Choisi" : "Choisir"}</span>
              </div>
              <p className={cx("mt-2 text-sm", theme.muted)}>{ex.text}</p>
            </button>
          );
        })}
      </div>
    </div>
  );
}

function QuestionField({
  q,
  value,
  onChange,
  theme,
}: {
  q: Question;
  value: any;
  onChange: (v: any) => void;
  theme: any;
}) {
  const baseInput =
    "mt-2 w-full rounded-xl border px-4 py-3 text-sm bg-transparent focus:outline-none focus:ring-2 focus:ring-[#D6B46A]/30";
  const border = theme.border;

  const label = (
    <div className="flex items-baseline justify-between gap-3">
      <label className="text-sm font-medium">
        {q.label} {q.optional ? <span className={cx("text-xs font-normal", theme.muted)}>(facultatif)</span> : null}
      </label>
    </div>
  );

  if (q.type === "text") {
    return (
      <div>
        {label}
        <input
          type="text"
          value={value ?? ""}
          placeholder={q.placeholder ?? ""}
          onChange={(e) => onChange(e.target.value)}
          className={cx(baseInput, border, theme.text)}
        />
      </div>
    );
  }

  if (q.type === "date") {
    return (
      <div>
        {label}
        <input
          type="date"
          value={value ?? ""}
          onChange={(e) => onChange(e.target.value)}
          className={cx(baseInput, border, theme.text)}
        />
      </div>
    );
  }

  if (q.type === "textarea") {
    return (
      <div>
        {label}
        <textarea
          value={value ?? ""}
          placeholder={q.placeholder ?? ""}
          onChange={(e) => onChange(e.target.value)}
          rows={4}
          className={cx(baseInput, border, theme.text)}
        />
      </div>
    );
  }

  if (q.type === "checkbox") {
    const arr: string[] = Array.isArray(value) ? value : [];
    return (
      <div>
        {label}
        <div className="mt-3 flex flex-wrap gap-2">
          {q.options.map((opt) => {
            const active = arr.includes(opt);
            return (
              <button
                key={opt}
                type="button"
                onClick={() => {
                  if (active) onChange(arr.filter((x) => x !== opt));
                  else onChange([...arr, opt]);
                }}
                className={cx(
                  "rounded-full border px-3 py-1 text-sm transition",
                  border,
                  active ? "bg-[#D6B46A] text-[#0A0F1A]" : "hover:opacity-90"
                )}
              >
                {opt}
              </button>
            );
          })}
        </div>
      </div>
    );
  }

  if (q.type === "radio") {
    return (
      <div>
        {label}
        <div className="mt-3 grid grid-cols-2 gap-2">
          {q.options.map((opt) => {
            const active = value === opt.value;
            return (
              <button
                key={opt.value}
                type="button"
                onClick={() => onChange(opt.value)}
                className={cx(
                  "rounded-xl border px-3 py-2 text-sm text-left transition",
                  border,
                  active ? "bg-[#D6B46A] text-[#0A0F1A]" : "hover:opacity-90"
                )}
              >
                {opt.label}
              </button>
            );
          })}
        </div>
      </div>
    );
  }

  return null;
}
